.features {
  padding-top: 125px;
  padding-bottom: 97px;
  background-color: $section-bg-secondary;

  @media screen and (max-width: 767px) {
    background-image: url(../images/mobile/features-2-320.png);
    background-position: 0px 766px;
    background-repeat: no-repeat;
    background-size: contain;
  }

  @media screen and (min-width: 767px) {
    padding-top: 130px;
    padding-bottom: 140px;
  }

  @media screen and (min-width: 1280px) {
    padding-top: 125px;
    padding-bottom: 139px;
    background-image: url(../images/desktop/features-2-1200.png);
    background-position: 0px 342px;
    background-repeat: no-repeat;
    background-size: contain;
  }
}

.features__subtitle.section__subtitle {
  margin-top: 0;
  margin-bottom: 15px;
  line-height: 1.17;

  @media screen and (min-width: 767px) {
    margin-bottom: 16px;
  }

  @media screen and (min-width: 1280px) {
    line-height: 1.13;
    margin-bottom: 15px;
  }
}

.features__heading.section__title {
  margin-top: 0;
  margin-bottom: 26px;

  @media screen and (min-width: 767px) {
    margin-bottom: 50px;
  }

  @media screen and (min-width: 1280px) {
    line-height: 1.14;
    margin-bottom: 55px;
  }
}

.features-discribe {
  margin-bottom: 39px;
  @media screen and (min-width: 767px) {
    width: 100%;
    display: flex;
  }
}

.features-text-container {
  padding: 0px 30px;

  @media screen and (min-width: 767px) {
    padding: 50px 0px 0px 0px;
    width: 340px;
    margin-left: 17px;
  }

  @media screen and (min-width: 1280px) {
    width: 460px;
    margin-left: 74px;
  }
}

.features__primary-text {
  margin-top: 46px;
  margin-bottom: 35px;
  @include font(500, 12px, 1.62);
  color: $black-color;

  @media screen and (min-width: 767px) {
    margin-top: 0;
    @include font(500, 14px, 1.76);
  }

  @media screen and (min-width: 1280px) {
    @include font(500, 16px, 1.76);
    letter-spacing: 0.04em;
  }
}

.features__secondary-text {
  @include font(500, 12px, 1.62);
  letter-spacing: 0.02em;
  color: #907e82;

  &.text-top {
    margin-bottom: 15px;
  }

  @media screen and (min-width: 767px) {
    @include font(500, 14px, 1.76);
    letter-spacing: 0.04em;
  }

  @media screen and (min-width: 1280px) {
    @include font(500, 14px, 1.94);
    &.text-top {
      margin-bottom: 20px;
    }
  }
}

.features__link.button {
  display: inline-flex;
  height: 44px;
  padding: 0px 37px;
  margin-top: 17px;
  border-radius: 22px;
  background-color: $white-color;
  margin-left: auto;
  margin-right: auto;

  &:hover,
  &:focus {
    background-color: $secondary-button-color;
  }

  @media screen and (min-width: 767px) {
    margin-top: 62px;
  }

  @media screen and (min-width: 1280px) {
    margin-top: 45px;
  }
}

.features__button-text {
  font-family: $font-family;
  @include font(700, 14px, 1.29);
  color: $primary-accent-color;

  &::after {
    content: '';
    display: inline-block;
    width: 7px;
    height: 10px;
    margin-left: 13px;
    background-image: url('../images/icons/Vector-pink.png');
  }
}

.features-list {
  margin-top: 39px;
  padding-right: 50px;

  @media screen and (min-width: 767px) {
    margin-top: 66px;
    display: flex;
    padding-right: 42px;
  }

  @media screen and (min-width: 1280px) {
    margin-top: 39px;
    padding-right: 0;
  }
}

.features-list__item {
  margin-bottom: 38px;
  &:last-child {
    margin-bottom: 0;
  }

  @media screen and (min-width: 767px) {
    width: calc((100% - 2 * 60px) / 3);
    margin-bottom: 0px;
    margin-right: 60px;

    &:last-child {
      margin-right: 0px;
    }
  }

  @media screen and (min-width: 1280px) {
    width: calc((100% - 2 * 30px) / 3);
    margin-right: 30px;

    &:last-child {
      margin-right: 0px;
    }
  }
}

.features-list__title {
  min-width: 98px;
  height: 56px;
  font-family: $font-family-second;
  font-style: normal;
  @include font(400, 36px, 1.14);
  letter-spacing: 0.04em;
  margin-top: 16px;
  color: $primary-accent-color;

  @media screen and (min-width: 767px) {
    @include font(400, 48px, 1.15);
    margin-top: 36px;
    margin-bottom: 13px;
  }

  @media screen and (min-width: 1280px) {
    @include font(400, 58px, 1.14);
    margin-top: 40px;
    margin-bottom: 15px;
  }
}

.features-list__text {
  @include font(700, 12px, 1.7);
  color: $black-color;

  @media screen and (min-width: 767px) {
    @include font(700, 14px, 1.74);
  }

  @media screen and (min-width: 1280px) {
    @include font(700, 16px, 1.76);
    letter-spacing: 0.02em;
  }
}

